.Header {
  background-color: var(--accent);
  color: white;
  position: sticky;
  top: 0;
  margin-bottom: var(--unit-4);
  height: 60px;
  z-index: var(--header-index);
}

.Container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.Left {
  display: flex;
  align-items: center;
  gap: var(--unit-3);
}

.Interval {
  font-size: var(--sm-text);
  color: var(--secondary);
}

.Title {
  font-weight: bold;
  /* text-transform: uppercase; */
}

.Subtitle {
  text-transform: uppercase;
  font-size: 0.5em;
}

.PanelAction {
  margin-bottom: var(--unit-3);
}
