.Footer {
  height: 60px;
  background-color: var(--accent);
  color: white;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

.Offset {
  height: 40px;
  margin-top: var(--unit-4);
}

.Container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0;
}

.Action {
  flex-grow: 0;
  flex-basis: 50%;
  padding: 0 var(--unit);
}

.Action:not(:first-child) {
  border-left: 1px outset;
}
